import React from 'react';
import CardWork from '../card-work/CardWork';
import style from './Works.module.css';

const Works = (props) => {
  return (
    <div className="container">
      <div className={style["works"]}>
        <CardWork 
          img         = "work.png"
          title       = "    "
          description = "      ,  :     ..."
        />
        <CardWork 
          img         = "work.png"
          title       = "    "
          description = "      ,  :     ..."
        />
        <CardWork 
          img         = "work.png"
          title       = "    "
          description = "      ,  :     ..."
        />
        <CardWork 
          img         = "work.png"
          title       = "    "
          description = "      ,  :     ..."
        />
      </div>
      {props.showLink === true ? <a className="page-link" href="#"> </a> : ''}
    </div>
  );
}

export default Works;
