import React from 'react';
import style from './Award.module.css';

const Award = () => {
  return (
    <div className={style['award']}>
      <div className={style["award-pdf"]}>
        <h3>PDF</h3>
      </div>
      <div className={style["award-info"]}>
        <p className={style["award-text"]}>
          : 2
        </p>
        <p className={style["award-text"]}>
          : " "
        </p>
        <p className={style["award-text"]}>
          :   
        </p>
        <p className={style["award-text"]}>
           :  18
        </p>
      </div>
    </div>
  );
}

export default Award;
